import React, { Component } from 'react';
import { Input, Button } from 'antd';

import style from './style.css';


const Search = Input.Search;

export default class searchHandle extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  search(value) {
    this.props.history.push(`/page/searchInfo?${value}`);
  }

  render() {
    return (
      <div className={style.bodystyle}>
        <div className={style.logo}>
          <div className={style.logoBox}>
            <img src="/img/duola_logo.png" alt="duola_logo"/>
          </div>
        </div>
        <div className={style.searchInput}>
          <Search
            placeholder="请输入商品名称"
            onSearch={value => this.search(value)}
            style={{ width: 200 }}
          />
        </div>
        <div className={style.jumpLink}>
          <a href="/page/arrivalList">到货信息总汇</a>
        </div>
      </div>
    );
  }
}