import React, { Component } from 'react';
import { Row, Col, Input, Modal, Radio } from 'antd';

import style from '../style.css';


export default class modalComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      changeData: {},
    }

    this.inputHandle = this.inputHandle.bind(this);
  }

  sendDataToFather() {

  }

  inputHandle(name, value) {
    this.setState({ newData: { ...this.state.newData, [name]: value } });
  }

  render() {
    const { itemData = {}, visible, upData, handleSubmit, handleCancel } = this.props;
    !itemData.status ? itemData.status = '' : itemData.status;
    return (
      <Modal
          title="编辑商品"
          visible={visible}
          closable={false}
          onOk={handleSubmit}
          onCancel={handleCancel}
      >
        <Row className={style.modalRow} type="flex" justify="space-around" align="middle">
          <Col span={6}>产品名称：</Col>
          <Col span={18}><Input value={itemData.name} onChange={(e) => upData('name', e.target.value)} /></Col>
        </Row>
        <Row className={style.modalRow} type="flex" justify="space-around" align="middle">
          <Col span={6}>产品规格：</Col>
          <Col span={18}><Input value={itemData.norm} onChange={(e) => upData('norm', e.target.value)} /></Col>
        </Row>
        <Row className={style.modalRow} type="flex" justify="space-around" align="middle">
          <Col span={6}>到货状态</Col>
          <Col span={18}>
            <Radio.Group defaultValue={itemData.status} buttonStyle="solid" size="small" onChange={(e) => upData('status', e.target.value)}>
              <Radio.Button value="">未到货</Radio.Button>
              <Radio.Button value="已到货">已到货</Radio.Button>
              <Radio.Button value="缺货中">缺货中</Radio.Button>
              <Radio.Button value="有余量">有余量</Radio.Button>
            </Radio.Group>
          </Col>
        </Row>
        <Row className={style.modalRow} type="flex" justify="space-around" align="middle">
          <Col span={6}>截团时间：</Col>
          <Col span={18}><Input value={itemData.endTime} onChange={(e) => upData('endTime', e.target.value)} /></Col>
        </Row>
        <Row className={style.modalRow} type="flex" justify="space-around" align="middle">
          <Col span={6}>到货时间：</Col>
          <Col span={18}><Input value={itemData.arrivalTime} onChange={(e) => upData('arrivalTime', e.target.value)} /></Col>
        </Row>
      </Modal>
    );
  }
}