import React, { Component } from 'react';
import { Row, Col, Breadcrumb, Icon } from 'antd';

import { Fetch } from 'fetchModules';
import style from './style.css';


export default class arrivalListHandle extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dateList: ['1','2'],
      dateDom: '',
      current: 1,
      total: 33
    };
    
    this.dateDom = this.dateDom.bind(this);
  }

  componentDidMount() {
    this.dateDom();
    this.fetchData();
  }

  async fetchData() {
    try {
      const res = await Fetch('/api/month/get');
      const { message: msg, status, result } = await res.json();
      if (!status) throw new Error(msg);
      this.dateDom(result);
    } catch (error) {
      console.log(error);
      return false;
    }
  }

  dateDom(result=[]) {
    const dateDom = result.map((item, key) => {
      const href = `/page/arrivalInfo?${item}`;
      return (<div key={key}><a href={href}>{item}到货信息总汇</a></div>)
    });
    this.setState({ dateDom });
  }

  render() {
    return [
      <Breadcrumb className={style.breadcrumb}>
        <Breadcrumb.Item href="/page/search">
          <Icon type="home" />
          <span>首页</span>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <Icon type="snippets" />
          <span>到货信息总汇</span>
        </Breadcrumb.Item>
      </Breadcrumb>,
      <Row className={style.list}>
        {this.state.dateDom}
      </Row>,
    ]
  }
}