import React, { Component } from 'react';
import { browserHistory } from 'react-router';
import { Row, Col, Breadcrumb, Icon, Pagination } from 'antd';

import { Fetch } from '../../staticFile/fetchModules';
import style from './style.css';


export default class searchHandle extends Component {
  constructor(props) {
    super(props);

    this.state = {
      time: decodeURI(this.props.history.location.search.split('?')[1]),
      htmlDom: '',
      current: 1,
      total: 3,
      defaultPageSize: 8,
    };

    this.showList = this.showList.bind(this);
  }

  componentDidMount() {
    this.showList();
    this.fetchData({ arrivalTime: this.state.time });
  }

  async fetchData(data) {
    const postData = {
      ...data,
      pageSize: this.state.defaultPageSize
    }
    try {
      const res = await Fetch('/api/monthList/get', postData);
      const { message: msg, status, result } = await res.json();
      if (!status) throw new Error(msg);
      const { commodity, total } = result;

      this.setState({ total });
      this.showList(commodity);
    } catch (error) {
      console.log(error);
    }
  }

  showList(data=[]) {
    const htmlDom = data.map((item, key) => {
      return (
        <Row id={key} className={style.infoLine}>
          <Row>
            <Col span={16}>{item.name}</Col>
            <Col span={8} className={style.textRight}>{item.endTime}</Col>
          </Row>
          <Row>
            <Col span={18}>{item.norm}</Col>
            <Col span={6} className={style.textRight}>{item.status}</Col>
          </Row>
        </Row>
      );
    })
    this.setState({ htmlDom });
  }
  
  changePage(page) {
    this.setState({ current: page });
    this.fetchData({ arrivalTime: this.state.time, current: page });
  }

  render() {
    return [
      <Breadcrumb className={style.breadcrumb}>
        <Breadcrumb.Item href="/page/search">
          <Icon type="home" />
          <span>首页</span>
        </Breadcrumb.Item>
        <Breadcrumb.Item href="/page/arrivalList">
          <Icon type="snippets" />
          <span>到货信息总汇</span>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <Icon type="copy" />
          <span>某订单到货信息</span>
        </Breadcrumb.Item>
      </Breadcrumb>,
      <Row className={style.list}>{this.state.htmlDom}</Row>,
      <Pagination
        className={style.pagination}
        current={this.state.current}
        defaultCurrent={1}
        total={this.state.total}
        defaultPageSize={this.state.defaultPageSize}
        hideOnSinglePage={true}
        onChange={(page) => this.changePage(page)}
        />
    ];
  }
}