import React from 'react';
import ReactDOM from 'react-dom';
import { Layout } from 'antd';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import searchPage from './modules/search';
import searchInfoPage from './modules/searchInfo';
import arrivalList from './modules/arrivalList';
import arrivalInfo from './modules/arrivalInfo';
import management from './modules/management';


ReactDOM.render((
  <div>
    <BrowserRouter>
      <Switch>
        <Route exact path='/' component={searchPage} />
        <Route path='/page/search' component={searchPage} />
        <Route path='/page/searchInfo' component={searchInfoPage} />
        <Route path='/page/arrivalList' component={arrivalList} />
        <Route path='/page/arrivalInfo' component={arrivalInfo} />
        <Route path='/sys/management' component={management} />
        <Route path='/sys/login' component={management} />
      </Switch>
    </BrowserRouter>
  </div>
), document.getElementById('root'));