import React, { Component } from 'react';
import { Row, Col, Input, Breadcrumb, Icon, Pagination, Button, message, Modal } from 'antd';

import MoadlCompoment from './component/modalComponent'
import { Fetch } from 'fetchModules';
import style from './style.css';


const Search = Input.Search;

export default class searchInfoHandle extends Component {
  constructor(props) {
    super(props);

    this.state = {
      keywords: '',
      htmlDom: '',
      current: 1,
      total: 0,
      defaultPageSize: 8,
      visible: false,
      checkVisible: false,
      itemData: {},
      checkPassword: ''
    };
    
    this.fetchData = this.fetchData.bind(this);
    this.searchData = this.searchData.bind(this);
    this.showList = this.showList.bind(this);
    this.changePage = this.changePage.bind(this);
    this.showModal = this.showModal.bind(this);
    this.updata = this.updata.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleOk = this.handleOk.bind(this);
    this.checkCancel = this.checkCancel.bind(this);
    this.checkInput = this.checkInput.bind(this);
  }

  componentDidMount() {
    this.fetchData({ name: '' });
  }

  async fetchData(data) {
    const postData = {
      ...data,
      pageSize: this.state.defaultPageSize
    };
    
    try {
      const res = await Fetch('/api/commodity/get', postData);
      const { message: msg, status, result } = await res.json();
      if (!status) throw new Error(msg);
      const { commodity, total } = result;

      this.setState({ total });
      this.showList(commodity);
    } catch (error) {
      console.log(error);
    }
  }

  showModal(item) {
    this.setState({ visible: true, itemData: item });
  };

  showList(result = []) {
    const htmlDom = result.map((item, key) => {
      return (
        <Row key={item._id} className={style.infoLine} type="flex" justify="space-around" align="middle">
          <Col span={20}>
            <Row>{item.name}</Row>
            <Row>{item.norm}</Row>
          </Col>
          <Col span={4} className={style.textRight}>
            <Button onClick={() => this.showModal(item)}>编辑</Button>
          </Col>
        </Row>
      );
    })
    this.setState({ htmlDom });
  }

  async searchData(name) {
    await this.fetchData({ name });
    this.setState({ keywords: name, current: 1 });
  }
  
  changePage(page) {
    this.setState({ current: page });
    this.fetchData({ name: this.state.keywords, current: page });
  }

  // 子页面Input值改变时触发变动
  updata(name, value) {
    this.setState({ itemData: { ...this.state.itemData, [name]: value } });
  }

  async handleSubmit() {
    //检查cookie
    const cookieVal = document.cookie;
    if(cookieVal === 'key=a18ae83bf9fc55bd3d99bf6f645a1208') {
      try {
        const res = await Fetch('/api/commodity/updata', this.state.itemData);
        const { message: msg, status } = await res.json();
        if (!status) throw new Error(msg);
  
        this.handleCancel();
        message.success('更新成功！');
        this.fetchData({ name: this.state.keywords, current: this.state.current });
      } catch (error) {
        console.log(error);
        message.error('更新信息失败！');
      }
    } else {
      this.setState({ checkVisible: true });
    }
    
  }
  handleCancel() {
    this.setState({ visible: false, checkVisible: false });
  }


  // 设置cookie，默认1天清除
  setCookie() {
    const d = new Date();
    // d.setTime(d.getTime() + (24 * 60 * 60 * 1000));
    d.setTime(d.getTime() + (7 * 24 * 60 *60 * 1000));
    const expires = "expires=" + d.toGMTString();
    document.cookie = `key=a18ae83bf9fc55bd3d99bf6f645a1208;${expires}`;
  }

  // 密码验证弹窗
  handleOk() {
    if(this.state.checkPassword === 'duola') {
      this.setCookie();
      this.checkCancel();
    } else {
      message.error('密码错误！')
    }
  }
  checkCancel() {
    this.setState({ checkVisible: false });
  }
  checkInput(value) {
    this.setState({ checkPassword: value });
  }

  render() {
    return [
      <Breadcrumb className={style.breadcrumb}>
        <Breadcrumb.Item href="/page/search">
          <Icon type="home" />
          <span>首页</span>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <Icon type="search" />
          <span>商品查询</span>
        </Breadcrumb.Item>
      </Breadcrumb>,
      <Row className={style.searchModel}>
        <Col span={4}></Col>
        <Col span={16}>
          <Search
            placeholder="输入要搜索的关键词"
            onSearch={value => this.searchData(value)}
            className={style.searchInput}
          />
        </Col>
        <Col span={4}></Col>
      </Row>,
      <Row>
        {this.state.htmlDom}
      </Row>,
      <Pagination
        className={style.pagination}
        current={this.state.current}
        defaultCurrent={1}
        total={this.state.total}
        defaultPageSize={this.state.defaultPageSize}
        hideOnSinglePage={true}
        onChange={(page) => this.changePage(page)}
      />,
      <MoadlCompoment 
        itemData={this.state.itemData}
        visible={this.state.visible}
        upData={this.updata}
        handleSubmit={this.handleSubmit}
        handleCancel={this.handleCancel}
      />,
      <Modal
          title="请输入验证密码"
          visible={this.state.checkVisible}
          onOk={this.handleOk}
          onCancel={this.checkCancel}
      >
        <Input placeholder="请输入密码" onChange={(e) => this.checkInput(e.target.value)} />
      </Modal>
    ];
  }
}